import Vue from 'vue'
import App from './App.vue'
// 
import './plugins'
// vue-x
import { store, initStore } from './store/index'
// vue-router
import { initRouter } from './router/index'
// 
import '@/styles/global.less'
// 
// import { $shared, SharedContainer } from '@/micro/shared'
import actions from '@/micro/actions'

Vue.config.productionTip = false

let ignoreRender = false
let instance = null
let router = null

function render (props) {
  router = initRouter()

  if (props) {
    //  actions 
    actions.setActions(props)
  }
  instance = new Vue({
    store,
    router,
    render: h => h(App),
    created () {
      // vuex
      initStore()
      // 
      initRender()
    }
  })
  if (window.__POWERED_BY_QIANKUN__) {
    const { container } = props
    instance.$mount(container ? container.querySelector('#app') : '#app')
  } else {
    instance.$mount('#app')
  }
}

function initRender () {
  if (!window.__POWERED_BY_QIANKUN__) {
    // 
    if (!window.custom.needToLogin) {
      store.dispatch('user/Navigation')
    }
  }
}

/* eslint-disable */
if (window.__POWERED_BY_QIANKUN__) {
  __webpack_public_path__ = window.__INJECTED_PUBLIC_PATH_BY_QIANKUN__
}
/* eslint-disable */

// 
if (!window.__POWERED_BY_QIANKUN__) {
	render()
}

// bootstrap 
// mountbootstrap
// unmount
export async function bootstrap () {
  // 
  // $shared.setSharedComponent(config.name, require('@/components/Shared/index.js'))
}

// mount
export async function mount (props) {
  ignoreRender = props.data && props.data.ignoreRender
  if (ignoreRender) return

  render(props)
}

// /
export async function unmount () {
  if (ignoreRender) return
  
  instance.$destroy()
  instance = null
  router = null
}