import config from '@/../package.json'
import Vue from 'vue'
import Router from 'vue-router'
import defaultRoutes from './routes'
import { store } from '@/store'
import { AppLayout, ViewLayout } from '@/components/Layout'

Vue.use(Router)

function createRouter () {
  return new Router({
    mode: 'history',
    base: window.__POWERED_BY_QIANKUN__ ? '/' + config.name : process.env.BASE_URL,
    routes: defaultRoutes
  })
}
let router = null
// 
export function initRouter () {
  router = createRouter()

  if (!window.__POWERED_BY_QIANKUN__) {
    router.beforeEach((to, from, next) => {
      let loggedIn = Vue.ss.get('loggedIn')
      if (loggedIn || !window.custom.needToLogin) {
        handleBeforeEach(to, from, next)
      } else {
        if (to.name === 'login') {
          next()
        } else {
          next({ path: '/login' })
        }
        Vue.ss.set('loggedIn', false)
      }
    })
  }

  function handleBeforeEach (to, from, next) {
    let getters = store.getters['user/GET_ROUTES']
    // 
    if (getters && Object.keys(getters).length > 0) {
      next()
      return
    }
    // 
    store.dispatch('user/Navigation')
    next()
  }
  
  return router
}
// routerConfig
let routes = {
  path: '/',
  name: '',
  meta: { title: '' },
  // ViewLayoutAppLayout
  component: window.__POWERED_BY_QIANKUN__ ? ViewLayout : AppLayout,
  children: []
}

// 
const routerMap = {
  'home': () => import('@/views/home/home.vue'),
  'largeScreen': () => import('@/views/largeScreen/largeScreen'),
  'error': () => import('@/views/error/error.vue')
}
// 
export function generatorRouter (data) {
  return new Promise((resolve, reject) => {
    data.forEach(route => {
      const currentRoute = {
        // target
        path: '/' + route.target,
        // 
        name: route.target,
        // 
        component: routerMap[route.target] ? routerMap[route.target] : routerMap['error'],
        meta: {
          title: route.title
        }
      }
      routes.children.push(currentRoute)
    })
    // 
    routes.redirect = routes.children[0].path
    resolve(routes)
  })
}
